import React, { useState, useEffect, useRef } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Badge } from '@/components/ui/badge';
import {
  Send,
  Phone,
  Mail,
  X,
  MessageSquare,
  Clock,
  Check,
  CheckCheck,
  AlertCircle,
  Loader2,
  Plus
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { toast } from 'sonner';
import {
  sendSMSToContact,
  getContactMessages,
  checkNewIncomingMessages,
  formatPhoneToE164,
  formatPhoneForDisplay,
  getUserPhoneNumbers,
  UserPhoneNumber
} from '@/api/services/messaging/messagingService';
import CreatePhoneNumberForm from '@/components/PhoneNumbers/CreatePhoneNumberForm';

interface Message {
  id: string;
  content: string;
  sender: 'user' | 'contact';
  timestamp: Date;
  status: 'sending' | 'sent' | 'delivered' | 'failed' | 'read';
  messageId?: string;
}

interface Contact {
  id: string;
  email: string;
  contact_number: string;
  list_name?: string;
}

interface ContactChatModalProps {
  isOpen: boolean;
  onClose: () => void;
  contact: Contact | null;
}

const ContactChatModal: React.FC<ContactChatModalProps> = ({
  isOpen,
  onClose,
  contact
}) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isSending, setIsSending] = useState(false);
  const [userPhoneNumbers, setUserPhoneNumbers] = useState<UserPhoneNumber[]>([]);
  const [isCreatePhoneModalOpen, setIsCreatePhoneModalOpen] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Load user phone numbers when modal opens
  useEffect(() => {
    if (isOpen) {
      loadUserPhoneNumbers();
    }
  }, [isOpen]);

  // Load message history when contact changes
  useEffect(() => {
    if (contact && isOpen) {
      loadMessageHistory();
    }
  }, [contact, isOpen]);

  // Set up efficient polling for incoming messages only
  useEffect(() => {
    if (!contact || !isOpen || !contact.id) return;

    // Set up polling every 2 seconds for new incoming messages only
    const pollInterval = setInterval(async () => {
      try {
        // Get the last message ID to check for new messages after it
        const lastMessage = messages[messages.length - 1];
        const lastMessageId = lastMessage?.id;

        console.log(`ðŸ” Polling for new incoming messages for contact ${contact.id} after ID: ${lastMessageId}`);

        const response = await checkNewIncomingMessages(contact.id, lastMessageId);

        if (response.success && response.hasNewMessages && response.messages.length > 0) {
          console.log(`ðŸ“¨ Found ${response.messages.length} new incoming messages`);

          // Add new incoming messages to the existing messages
          const newMessages = response.messages.map(msg => ({
            id: msg.id.toString(),
            content: msg.content,
            sender: msg.sender as 'user' | 'contact',
            timestamp: new Date(msg.timestamp),
            status: msg.status as 'sending' | 'sent' | 'delivered' | 'failed' | 'read',
            messageId: msg.messageId
          }));

          setMessages(prev => [...prev, ...newMessages]);

          // Show notification for new messages
          toast.success(`New message from ${contact.email?.split('@')[0] || 'Contact'}`);
        }
      } catch (error) {
        console.error('Error polling for new messages:', error);
        // Don't show error toast to avoid spam
      }
    }, 2000);

    return () => {
      clearInterval(pollInterval);
    };
  }, [contact?.id, isOpen, messages]);

  const loadMessageHistory = async () => {
    if (!contact) return;

    setIsLoading(true);
    try {
      const response = await getContactMessages(contact.id);
      if (response.success) {
        const formattedMessages: Message[] = response.messages.map((msg: any) => ({
          id: msg.id.toString(),
          content: msg.content,
          sender: msg.sender,
          timestamp: new Date(msg.timestamp),
          status: msg.status,
          messageId: msg.message_id
        }));
        setMessages(formattedMessages);
      } else {
        // If no messages found, start with empty array
        setMessages([]);
      }
    } catch (error) {
      console.error('Failed to load message history:', error);
      toast.error('Failed to load message history');
      setMessages([]);
    } finally {
      setIsLoading(false);
    }
  };

  const loadUserPhoneNumbers = async () => {
    try {
      const phoneNumbers = await getUserPhoneNumbers();
      setUserPhoneNumbers(phoneNumbers);
    } catch (error) {
      console.error('Failed to load user phone numbers:', error);
      // Don't show error toast as this is called automatically
    }
  };

  const sendMessage = async () => {
    if (!newMessage.trim() || !contact || isSending) return;

    // Check if user has phone numbers available
    if (userPhoneNumbers.length === 0) {
      toast.error('No phone numbers available. Please add a phone number first.');
      return;
    }

    const messageContent = newMessage.trim();
    setNewMessage('');
    setIsSending(true);

    // Create temporary message with sending status
    const tempMessage: Message = {
      id: Date.now().toString(),
      content: messageContent,
      sender: 'user',
      timestamp: new Date(),
      status: 'sending'
    };

    setMessages(prev => [...prev, tempMessage]);

    try {
      // Format phone number to E.164 format
      const formattedPhone = formatPhoneToE164(contact.contact_number);

      // Send SMS using the messaging service
      const result = await sendSMSToContact({
        toPhoneNumber: formattedPhone,
        message: messageContent,
        contactId: contact.id
      });

      if (result.success) {
        // Update message status to sent
        setMessages(prev =>
          prev.map(msg =>
            msg.id === tempMessage.id
              ? { ...msg, status: 'sent', messageId: result.messageId }
              : msg
          )
        );
        toast.success('Message sent successfully');
      } else {
        throw new Error(result.error || 'Failed to send message');
      }
    } catch (error) {
      console.error('Failed to send message:', error);

      // Update message status to failed
      setMessages(prev =>
        prev.map(msg =>
          msg.id === tempMessage.id
            ? { ...msg, status: 'failed' }
            : msg
        )
      );

      toast.error('Failed to send message');
    } finally {
      setIsSending(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const getInitials = (email: string) => {
    const name = email.split('@')[0];
    return name.substring(0, 2).toUpperCase();
  };

  const formatPhoneNumber = (phone: string) => {
    if (!phone) return '';
    return formatPhoneForDisplay(phone);
  };

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  const getMessageStatusIcon = (status: Message['status']) => {
    switch (status) {
      case 'sending':
        return <Loader2 className="h-3 w-3 animate-spin text-gray-400" />;
      case 'sent':
        return <Check className="h-3 w-3 text-gray-400" />;
      case 'delivered':
        return <CheckCheck className="h-3 w-3 text-blue-500" />;
      case 'failed':
        return <AlertCircle className="h-3 w-3 text-red-500" />;
      default:
        return null;
    }
  };

  if (!contact) return null;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl h-[600px] p-0 flex flex-col">
        {/* Header */}
        <DialogHeader className="p-4 border-b bg-gray-50 flex-shrink-0">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <Avatar className="h-10 w-10 bg-gradient-to-br from-primary/20 to-primary/10">
                <AvatarFallback className="text-primary font-medium">
                  {getInitials(contact.email)}
                </AvatarFallback>
              </Avatar>
              <div>
                <DialogTitle className="text-lg font-semibold text-gray-900">
                  {contact.email.split('@')[0]}
                </DialogTitle>
                <div className="flex items-center space-x-4 text-sm text-gray-500">
                  <div className="flex items-center space-x-1">
                    <Mail className="h-3 w-3" />
                    <span>{contact.email}</span>
                  </div>
                  <div className="flex items-center space-x-1">
                    <Phone className="h-3 w-3" />
                    <span>{formatPhoneNumber(contact.contact_number)}</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="flex items-center space-x-2">
              {contact.list_name && (
                <Badge variant="secondary" className="text-xs">
                  {contact.list_name}
                </Badge>
              )}
              <Button
                variant="ghost"
                size="sm"
                onClick={onClose}
                className="h-8 w-8 p-0"
              >
                <X className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </DialogHeader>

        {/* Messages Area */}
        <div className="flex-1 overflow-y-auto p-4 bg-gray-50">
          {messages.length === 0 ? (
            <div className="flex flex-col items-center justify-center h-full text-gray-400">
              <div className="w-16 h-16 bg-white rounded-full flex items-center justify-center mb-4 shadow-sm">
                <MessageSquare className="h-8 w-8" />
              </div>
              <p className="text-center text-sm">
                No messages yet. Start a conversation!
              </p>
            </div>
          ) : (
            <div className="space-y-4">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={cn(
                    'flex',
                    message.sender === 'user' ? 'justify-end' : 'justify-start'
                  )}
                >
                  <div
                    className={cn(
                      'max-w-[70%] rounded-lg px-4 py-2 shadow-sm',
                      message.sender === 'user'
                        ? 'bg-primary text-primary-foreground'
                        : 'bg-white text-gray-900 border'
                    )}
                  >
                    <p className="text-sm">{message.content}</p>
                    <div className={cn(
                      'flex items-center justify-end space-x-1 mt-1',
                      message.sender === 'user' ? 'text-primary-foreground/70' : 'text-gray-500'
                    )}>
                      <span className="text-xs">{formatTime(message.timestamp)}</span>
                      {message.sender === 'user' && getMessageStatusIcon(message.status)}
                    </div>
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
          )}
        </div>

        {/* Message Input */}
        <div className="p-4 border-t bg-white flex-shrink-0">
          {/* Show Add Phone Number link if no phone numbers available */}
          {userPhoneNumbers.length === 0 && (
            <div className="mb-3">
              <div className="flex items-center justify-center p-3 border border-dashed border-gray-300 rounded bg-gray-50 hover:bg-gray-100 transition-colors">
                <Button
                  onClick={() => setIsCreatePhoneModalOpen(true)}
                  variant="ghost"
                  size="sm"
                  className="flex items-center space-x-2 text-blue-600 hover:text-blue-700 font-medium"
                >
                  <Plus className="h-4 w-4" />
                  <span>Add Phone Number to Send Messages</span>
                </Button>
              </div>
            </div>
          )}

          <div className="flex items-center space-x-2">
            <Input
              value={newMessage}
              onChange={(e) => setNewMessage(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder={userPhoneNumbers.length === 0 ? "Add a phone number to send messages..." : "Type your message..."}
              className="flex-1"
              disabled={isSending || userPhoneNumbers.length === 0}
            />
            <Button
              onClick={sendMessage}
              disabled={!newMessage.trim() || isSending || userPhoneNumbers.length === 0}
              size="sm"
              className="px-3"
            >
              {isSending ? (
                <Loader2 className="h-4 w-4 animate-spin" />
              ) : (
                <Send className="h-4 w-4" />
              )}
            </Button>
          </div>
          <p className="text-xs text-gray-500 mt-2">
            Messages will be sent via SMS to {formatPhoneNumber(contact.contact_number)}
          </p>
        </div>
      </DialogContent>

      {/* Create Phone Number Modal */}
      <Dialog open={isCreatePhoneModalOpen} onOpenChange={setIsCreatePhoneModalOpen}>
        <DialogContent className="max-w-7xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>Create New Phone Number</DialogTitle>
            <DialogDescription>
              Get a free AI CRUITMENT phone number to send SMS messages.
            </DialogDescription>
          </DialogHeader>
          <CreatePhoneNumberForm
            onSuccess={() => {
              setIsCreatePhoneModalOpen(false);
              // Reload phone numbers after successful creation
              loadUserPhoneNumbers();
            }}
            onCancel={() => setIsCreatePhoneModalOpen(false)}
          />
        </DialogContent>
      </Dialog>
    </Dialog>
  );
};

export default ContactChatModal;
