import React, { useState } from 'react';
import Page from '@/components/Page';
import Header from '@/components/Header';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import {
  MessageSquare,
  Phone,
  Mail,
  Settings,
  Plus,
  Users
} from 'lucide-react';
import ConversationsList from '@/components/ContactMessaging/ConversationsList';
import ContactChatModal from '@/components/ContactMessaging/ContactChatModal';

interface Contact {
  id: string;
  email: string;
  contact_number: string;
  list_name?: string;
}

const Messages: React.FC = () => {
  const [selectedContact, setSelectedContact] = useState<Contact | null>(null);
  const [isChatModalOpen, setIsChatModalOpen] = useState(false);

  const handleSelectConversation = (contact: Contact) => {
    setSelectedContact(contact);
    setIsChatModalOpen(true);
  };

  const handleCloseChat = () => {
    setIsChatModalOpen(false);
    setSelectedContact(null);
  };

  return (
    <Page >
      {/* Neon glow layer */}
      <div aria-hidden className="absolute inset-0 pointer-events-none bg-gradient-to-r from-indigo-500/10 via-purple-500/10 to-pink-500/10" />

      <div className="relative z-10">
        <Header
          title="Messages"
          buttonText="New Message"
          filterByName={false}
          action={() => {
            // TODO: Implement new message functionality
            console.log('New message clicked');
          }}
        />

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 h-[calc(100vh-200px)]">
          {/* Conversations Sidebar */}
          <div className="lg:col-span-1">
            <Card className="h-full bg-white/5 backdrop-blur-md border border-white/10">
              <ConversationsList
                onSelectConversation={handleSelectConversation}
                selectedContactId={selectedContact?.id}
              />
            </Card>
          </div>

          {/* Main Chat Area */}
          <div className="lg:col-span-2">
            <Card className="h-full flex flex-col bg-white/5 backdrop-blur-md border border-white/10">
              {selectedContact ? (
                <div className="h-full flex flex-col">
                  {/* Chat Header */}
                  <div className="p-4 border-b border-white/10 bg-white/5">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <div className="w-10 h-10 bg-indigo-500/20 rounded-full flex items-center justify-center">
                          <MessageSquare className="h-5 w-5 text-indigo-400" />
                        </div>
                        <div>
                          <h3 className="font-semibold text-white">
                            {selectedContact.email.split('@')[0]}
                          </h3>
                          <div className="flex items-center space-x-4 text-sm text-gray-400">
                            <div className="flex items-center space-x-1">
                              <Mail className="h-3 w-3" />
                              <span>{selectedContact.email}</span>
                            </div>
                            <div className="flex items-center space-x-1">
                              <Phone className="h-3 w-3" />
                              <span>{selectedContact.contact_number}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div className="flex items-center space-x-2">
                        {selectedContact.list_name && (
                          <Badge variant="secondary" className="bg-purple-500/20 text-purple-300 border border-purple-500/30">
                            {selectedContact.list_name}
                          </Badge>
                        )}
                        <Button variant="ghost" size="sm" className="text-gray-400 hover:text-white hover:bg-white/10">
                          <Settings className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </div>

                  {/* Chat Messages Area */}
                  <div className="flex-1 p-4 bg-white/5 flex items-center justify-center">
                    <div className="text-center text-gray-400">
                      <MessageSquare className="h-12 w-12 mx-auto mb-4 text-gray-500" />
                      <p className="text-lg font-medium mb-2 text-white">Open Full Chat</p>
                      <p className="text-sm mb-4 text-gray-400">
                        Click the button below to open the full messaging interface
                      </p>
                      <Button onClick={() => setIsChatModalOpen(true)} className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0">
                        <MessageSquare className="h-4 w-4 mr-2" />
                        Open Chat
                      </Button>
                    </div>
                  </div>
                </div>
              ) : (
                <div className="h-full flex items-center justify-center">
                  <div className="text-center text-gray-400">
                    <div className="w-20 h-20 bg-white/10 rounded-full flex items-center justify-center mx-auto mb-6">
                      <MessageSquare className="h-10 w-10 text-gray-500" />
                    </div>
                    <h3 className="text-xl font-medium mb-2 text-white">Welcome to Messages</h3>
                    <p className="text-gray-400 mb-6 max-w-md">
                      Select a conversation from the sidebar to start messaging with your contacts via SMS.
                    </p>
                    <div className="space-y-3">
                      <div className="flex items-center justify-center space-x-2 text-sm text-gray-400">
                        <Phone className="h-4 w-4" />
                        <span>SMS messaging powered by Twilio</span>
                      </div>
                      <div className="flex items-center justify-center space-x-2 text-sm text-gray-400">
                        <Users className="h-4 w-4" />
                        <span>Message history stored securely</span>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </Card>
          </div>
        </div>

        {/* Chat Modal */}
        <ContactChatModal
          isOpen={isChatModalOpen}
          onClose={handleCloseChat}
          contact={selectedContact}
        />
      </div>
    </Page>
  );
};

export default Messages;
