import React, { useState, useEffect } from 'react';
import { Card } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  MessageSquare,
  Search,
  Clock,
  Phone,
  Mail,
  Loader2,
  RefreshCw
} from 'lucide-react';
import { getConversations } from '@/api/services/messaging/messagingService';
import { formatPhoneForDisplay } from '@/api/services/messaging/messagingService';
import { toast } from 'sonner';

interface Conversation {
  contact_id: string;
  email: string;
  contact_number: string;
  list_name?: string;
  last_message: string;
  last_message_time: string;
  last_message_sender: 'user' | 'contact';
  total_messages: number;
  unread_count: number;
}

interface ConversationsListProps {
  onSelectConversation: (contact: any) => void;
  selectedContactId?: string;
}

const ConversationsList: React.FC<ConversationsListProps> = ({
  onSelectConversation,
  selectedContactId
}) => {
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [isRefreshing, setIsRefreshing] = useState(false);

  useEffect(() => {
    loadConversations();
  }, []);

  const loadConversations = async () => {
    try {
      setIsLoading(true);
      const response = await getConversations();
      if (response.success) {
        setConversations(response.conversations);
      }
    } catch (error) {
      console.error('Failed to load conversations:', error);
      toast.error('Failed to load conversations');
    } finally {
      setIsLoading(false);
    }
  };

  const handleRefresh = async () => {
    setIsRefreshing(true);
    await loadConversations();
    setIsRefreshing(false);
  };

  const getInitials = (email: string) => {
    const name = email.split('@')[0];
    return name.substring(0, 2).toUpperCase();
  };

  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60);

    if (diffInHours < 1) {
      return 'Just now';
    } else if (diffInHours < 24) {
      return `${Math.floor(diffInHours)}h ago`;
    } else if (diffInHours < 168) { // 7 days
      return `${Math.floor(diffInHours / 24)}d ago`;
    } else {
      return date.toLocaleDateString();
    }
  };

  const filteredConversations = conversations.filter(conv =>
    conv.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
    conv.contact_number.includes(searchQuery) ||
    conv.last_message.toLowerCase().includes(searchQuery.toLowerCase())
  );

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="flex items-center space-x-2 text-gray-500">
          <Loader2 className="h-5 w-5 animate-spin" />
          <span>Loading conversations...</span>
        </div>
      </div>
    );
  }

  return (
    <div className="h-full flex flex-col">
      {/* Header */}
      <div className="p-4 border-b bg-white">
        <div className="flex items-center justify-between mb-3">
          <h2 className="text-lg font-semibold text-gray-900">Messages</h2>
          <Button
            variant="ghost"
            size="sm"
            onClick={handleRefresh}
            disabled={isRefreshing}
            className="h-8 w-8 p-0"
          >
            <RefreshCw className={`h-4 w-4 ${isRefreshing ? 'animate-spin' : ''}`} />
          </Button>
        </div>
        
        {/* Search */}
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
          <Input
            placeholder="Search conversations..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-10"
          />
        </div>
      </div>

      {/* Conversations List */}
      <div className="flex-1 overflow-y-auto">
        {filteredConversations.length === 0 ? (
          <div className="flex flex-col items-center justify-center h-full text-gray-400 p-8">
            <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
              <MessageSquare className="h-8 w-8" />
            </div>
            <p className="text-center text-sm">
              {searchQuery ? 'No conversations match your search' : 'No conversations yet'}
            </p>
          </div>
        ) : (
          <div className="space-y-1 p-2">
            {filteredConversations.map((conversation) => (
              <Card
                key={conversation.contact_id}
                className={`p-3 cursor-pointer transition-colors hover:bg-gray-50 ${
                  selectedContactId === conversation.contact_id 
                    ? 'bg-primary/5 border-primary/20' 
                    : 'border-gray-200'
                }`}
                onClick={() => onSelectConversation({
                  id: conversation.contact_id,
                  email: conversation.email,
                  contact_number: conversation.contact_number,
                  list_name: conversation.list_name
                })}
              >
                <div className="flex items-start space-x-3">
                  <Avatar className="h-10 w-10 bg-gradient-to-br from-primary/20 to-primary/10 flex-shrink-0">
                    <AvatarFallback className="text-primary font-medium">
                      {getInitials(conversation.email)}
                    </AvatarFallback>
                  </Avatar>
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between mb-1">
                      <h3 className="font-medium text-gray-900 truncate">
                        {conversation.email.split('@')[0]}
                      </h3>
                      <div className="flex items-center space-x-2">
                        {conversation.unread_count > 0 && (
                          <Badge variant="destructive" className="text-xs px-1.5 py-0.5">
                            {conversation.unread_count}
                          </Badge>
                        )}
                        <span className="text-xs text-gray-500">
                          {formatTime(conversation.last_message_time)}
                        </span>
                      </div>
                    </div>
                    
                    <div className="flex items-center space-x-2 text-xs text-gray-500 mb-2">
                      <Phone className="h-3 w-3" />
                      <span>{formatPhoneForDisplay(conversation.contact_number)}</span>
                      {conversation.list_name && (
                        <>
                          <span>â€¢</span>
                          <Badge variant="secondary" className="text-xs">
                            {conversation.list_name}
                          </Badge>
                        </>
                      )}
                    </div>
                    
                    <div className="flex items-center space-x-2">
                      <div className={`w-2 h-2 rounded-full flex-shrink-0 ${
                        conversation.last_message_sender === 'user' 
                          ? 'bg-blue-500' 
                          : 'bg-green-500'
                      }`} />
                      <p className="text-sm text-gray-600 truncate">
                        {conversation.last_message}
                      </p>
                    </div>
                    
                    <div className="flex items-center justify-between mt-2">
                      <span className="text-xs text-gray-400">
                        {conversation.total_messages} message{conversation.total_messages !== 1 ? 's' : ''}
                      </span>
                      <div className="flex items-center space-x-1 text-xs text-gray-400">
                        <Clock className="h-3 w-3" />
                        <span>
                          {conversation.last_message_sender === 'user' ? 'You' : 'Contact'}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default ConversationsList;
